@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.content{
  position: absolute;
  left:50%;
  top:30%;
  -webkit-transform: translate(-50%,-30%);
          transform: translate(-50%,-30%);
  border: 2px solid #B7AEAE;
  border-radius: 20px;
  padding: 30px 100px 90px 100px;
  background-color: #dbd6d68f;
}
body{
  background-color: #EEECEC;
}
h1{
  text-align: center;
}
.todo-item{
  position: relative;
  display: -webkit-flex;
  display: flex;
  padding: 20px 0px;
  border: 1px solid #CAC6C6;
  background-color: #fff;
}
.todo-text{
  margin: 0 0 0 40px;
}
.todo-text.active{
  text-decoration: line-through;
}
.input{
  width: 350px;
  font-size: 18px;
  border-radius: 10px;
  margin: 0 30px 0 0;
  font-family: 'Roboto', sans-serif;
  padding:10px;
  outline: none;
  border: 1px solid #B7AEAE;
}
.todo-text,.todo_account{
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
}
.todo_account{
  position: absolute;
  left:-30px;
  bottom: 15px;
  font-size: 30px;
}
.btn{
  display: inline-block;
  height: 35px;
  line-height: 35px;
  padding: 0 20px;
  background-color: #A45D5D;
  border-radius: 10px;
  cursor: pointer;
  outline: none;
  border: none;
}

.btn span{
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-size: 18px;
}
.todo_trash{
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.todo_trash img{
  width: 19px;
  height: 24px;
}
.todo_mark img{
  width: 18px;
  height: 18px;
}
.todo_mark{
  position: absolute;
  left:10px;
  cursor: pointer;
}
.empty_block{
  position: absolute;
  left:10px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  border: 1px solid black;
}
